
/*home box-------------------------------*/
.home-box{
	background-color: white;
}

/*home image----------------------------*/
.home-image-box{
/*	height: 41.35vw;*/
}
.home-image-box img{
	width: 90%;
	height: 100%;
	display: block;
	margin: auto;
	filter: drop-shadow(.2vw .2vw 1vw rgb(94,23,235,.7));
}

/*home name-----------------------------------------------------------------*/
.home-name-box h2{
	font-size: 3vw;
	font-family: 'Poppins',sans-serif;
}
.home-name-box h1{
	margin-top: 1.5vw;
	font-size: 6vw;
	font-family: 'Poppins',sans-serif;
	font-weight: bold;
	color: rgb(94,23,235,1);
}
.home-name-box h3{
	font-size: 2vw;
	font-family: 'Poppins',sans-serif;
}
.home-name-box button{
	border: none;
	display: block;
	display: inline-block;
	width: 40%;
	background-color: white;
	font-size: 1.5vw;
	border: .2vw solid rgb(94,23,235,1);
	padding: .3vw 2vw;
	font-weight: 500;
	border-radius: 10vw;
	margin: 2vw 0vw;
	margin-right: 1.5vw;
	transition: .2s linear;
	color: rgb(94,23,235,1);
}
.home-name-box button:hover{
	background-color: rgb(94,23,235,1);
	color: white;
	transition: .2s linear;
}
.home-name-box .nav-social-icon{
	color: rgb(94,23,235,1);
	margin-left: 0vw;
	margin-right: 2.7vw;
}
.home-name-box .nav-social-icon:hover{
	color: rgb(0,0,0,1);
}

/*media query*********************************************************************/
/*extra small*/
@media screen and (max-width:576px){
/*	home---------------------------------------------*/
	.home-box{
		padding: 5vw 1vw;
	}
	.home-image-box{
/*	  height: 110vw;*/
	}
	.home-image-box img{
	width: 100%;
	height: 100%;
	}

/*home name-----------------------------------------------------------------*/
	.home-name-box{
		padding: 4vw 2vw;
	}
	.home-name-box h2{
		font-size: 10vw;
	}
	.home-name-box h1{
		margin-top: 2.5vw;
		font-size: 21vw;
	}
	.home-name-box h3{
		font-size: 6.5vw;
	}
	.home-name-box button{
		font-size: 4.5vw;
		border: .4vw solid rgb(94,23,235,1);
		padding: 1.3vw 6vw;
		border-radius: 5vw;
		margin: 6vw 0vw;
		width: 47%;
		margin-right: 2vw;
	}
	.home-name-box .nav-social-icon{
		color: rgb(94,23,235,1);
		font-size: 12vw;
		margin-left: 0vw ;
		margin-right: 7vw ;
	}

}


